@import '../../styles/variables.module.scss';

.avatar {
    list-style-type: none;
}

.userMenuItem {
    list-style: none;
    min-width: 100%;
    display: flex;
    align-items: center;
    height: 32px;
}

.name__email {
    font-size: $fontSize-xxsmall !important;
    color: #a6a5a5;
    text-decoration: none !important;
}

.avatar {
    margin-top: $indent-small;

    a {
        display: flex;
        flex-direction: row;
        min-width: 100%;
        height: 48px;
        justify-content: flex-start;
        align-items: center;

        .userMenuItem__name {
            margin-left: 10px;
        }
    }
}

.userMenuItem__name {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-left: 50px;
    color: #3e77aa;

    &:hover {
        .name__name {
            color: #f84147;
            text-decoration: underline;
        }
    }
}

.userMenuItem__icon {
    display: flex;
    flex-direction: row;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #3e77aa;
    font-size: 24px;
    color: #fff;
}